import tagSexAge from '@/components/common/tag-sex-age.vue'
export default {
	props:{
		item: Object,
		itemClass: {
			type: String,
			default: 'fadeIn'
		}
	},
	data () {
		return {}
	},
	mounted () {
		console.log(this.item)
	},
	components: {
		tagSexAge
	},
	computed: {
		getSex() {
			return this.item.sex;
		},
		getAge(){
			return this.item.age;
		}
	},
	methods: {
		async follow () {
			try{
				let [err,res] = await this.$http.post('/follow',{
					follow_id:this.item.userid
				},{
					token:true,
					checkToken:true,
					checkAuth:true
				});
				// 错误处理
				if (!this.$http.errorCheck(err,res)) return;
				// 通知首页修改数据
				uni.showToast({ title: '关注成功' });
				let resdata = {
					type:"guanzhu",
					userid:this.item.userid,
					data:true
				};
				// 通知父组件
				this.$emit('changeEvent',resdata);
				// 全局通知
				uni.$emit('updateData',resdata);
			}catch(e){ return; }
		},
		imgDetail (index) {
			uni.previewImage({
				urls:this.item.morePic,
				current:index
			})
		},
		async caozuo(type){
			let index = (type === 'smile') ? 1 : 2; // 当前操作
			let [err,res] = await this.$http.post('/support',{
				post_id:this.item.id,
				type:index-1
			},{
				token:true,
				checkToken:true,
				checkAuth:true
			});
			if (!this.$http.errorCheck(err,res)) return;
			uni.showToast({ title: "顶成功" });
			// 通知父组件
			let resdata = {
				type:"support",
				post_id:this.item.id,
				do:type
			};
			this.$emit('changeEvent',resdata);
			// 通知全局
			return uni.$emit("updateData",resdata);
		},
	}
}
